@import url(Common.scss);

.chatting{

    height: 100%;

    // header
    .header{
        z-index: 10;
        color: #000;
        background-color: #a1c0d5;
        border-bottom: 1px solid #96acba;

        .header_title{
            .left_item{
                a{
                    opacity: 1;
                    color: #000;
                }
            }
            .right_item{
                a{
                    color: #000;
                    span{
                        color: #000;
                    }
                }
            }
        }
    }
    
    /* main */
    .chatting_main{
        display: flex;
        flex-direction: column;
        height: calc(100% - 109px);
        padding-top: 64px;
        background-color: #a1c0d5;

        .date_info{
            opacity: 0.5;
            text-align: center;
            margin: 10px;
        }

        .chat_box{
            display: inline-flex;   /* 내용물 크기만큼 크기가 잡히는 flex / 그냥 flex는 가로 100% */
            flex-direction: column;
            align-items: flex-start;    /* 채팅 상자가 왼쪽을 기준으로  */
            width: fit-content;
            max-width: calc(100% - 80px);  /* 내용이 길어져도 오른쪽 여백이 남음  */
            position: relative;

            .chat{
                font-size: 14px;
                line-height: 1.2;
                margin: 5px;
                padding: 7px;
                border-radius: 5px;
                background-color: #fff;
            }
        }
        
        /* my */
        .my{
            align-self: flex-end; /* my 채팅 내용의 모든 줄이 오른쪽을 기준으로   */
            align-items: flex-end; /* my 채팅 상자가 오른쪽을 기준으로   */
            max-width: 240px;
            margin-right: 10px;

            .chat{
                background-color: #ffe73a;
            }

            .chat_time{
                position: absolute;
                right: 100%;
                bottom: 5px;
                width: 50px;
                opacity: 0.5;
            }
        }

        /* other */
        .other{
            margin-left: 55px;
            margin-top: 20px;

            .profile_img{
                position: absolute;
                left: -55px;
                top: -5px;
                width: 35px;
                height: 35px;
            }
            .profile_name{
                display: block;
                font-weight: bold;
                margin: 5px;
            }
            .other .chat_time{
                position: absolute;
                left: 100%;
                bottom: 5px;
                width: 50px;
                opacity: 0.5;
            }
        }

    }

    /* footer */
    .chatting_footer{
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 45px;
        background-color: #eee;

        .plus_btn a{
            color: #b3b4b4;
            padding-left: 15px;
            font-size: 14px;
        }

        form{
            display: flex;
            justify-content: flex-end;
            width: calc(100% - 40px);

            fieldset.text_box{
                position: relative;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                width: 100%;

                .text_field{
                    width: calc(100% - 40px);
                    height: 32px;
                    padding: 0 5px;
                    border: rgb(201, 201, 201);
                }

                .emoticon_btn{
                    position: absolute;
                    right: 45px;

                    a{
                        color: #b3b4b4;
                    }
                }

                .voice_btn a{
                    display: block;
                    width: 34px;
                    height: 37px;
                    margin-right: 5px;
                    line-height: 37px;
                    text-align: center;
                    background-color: #ffe73a;
                    color: #513736;
                    font-size: 14px;
                }
            }
        }
    }

}



